<template>
	<view class="wrap">
		<view class="top"></view>
		<view class="content">
			<view class="title">欢迎登录</view>
			<input class="u-border-bottom" type="number" v-model="tel" placeholder="请输入手机号" />
			<view class="tips">未注册的手机号验证后自动创建账号</view>
			<button @tap="submit" :style="[inputStyle]" class="getCaptcha">获取短信验证码</button>
			<view class="alternative">
				<view class="password" @click="loginByPwd">密码登录</view>
				<view class="issue" @click="register">账号注册</view>
			</view>
		</view>
		<view class="buttom">
			<view class="loginType">
				<view class="wechat item">
					<view class="icon"><u-icon size="70" name="weixin-fill" color="rgb(83,194,64)"></u-icon></view>
					微信
				</view>
				<view class="QQ item">
					<view class="icon"><u-icon size="70" name="qq-fill" color="rgb(17,183,233)"></u-icon></view>
					QQ
				</view>
			</view>
		</view>
    <view>
      <u-toast ref="uToast" />
    </view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				tel: ''
			}
		},
    computed: {
    	inputStyle() {
    		let style = {};
    		if(this.tel) {
    			style.color = "#fff";
    			style.backgroundColor = this.$u.color['warning'];
    		}
    		return style;
    	}
    },
		methods: {
      // 获取手机验证码
			submit() {
				if(this.$u.test.mobile(this.tel)) {
					this.$u.route({
						url: 'pages/user/code?tel='+this.tel
					});
				} else {
          this.$refs.uToast.show({
          	title: '请输入正确格式的手机号',
          	type: 'error'
          });
        }
			},
      // 跳转到通过密码登录界面
      loginByPwd() {
        this.$u.route({
        	url: 'pages/user/loginByPwd'
        });
      },
      // 跳转注册界面
      register() {
        this.$u.route({
        	url: 'pages/user/register'
        });
      }
		}
	}
</script>


<style lang="scss" scoped>
.wrap {
	font-size: 28rpx;
	.content {
		width: 600rpx;
		margin: 80rpx auto 0;

		.title {
			text-align: left;
			font-size: 60rpx;
			font-weight: 500;
			margin-bottom: 100rpx;
		}
		input {
			text-align: left;
			margin-bottom: 10rpx;
			padding-bottom: 6rpx;
		}
		.tips {
			color: $u-type-info;
			margin-bottom: 60rpx;
			margin-top: 8rpx;
		}
		.getCaptcha {
			background-color: rgb(253, 243, 208);
			color: $u-tips-color;
			border: none;
			font-size: 30rpx;
			padding: 12rpx 0;
			
			&::after {
				border: none;
			}
		}
		.alternative {
			color: $u-tips-color;
			display: flex;
			justify-content: space-between;
			margin-top: 30rpx;
		}
	}
	.buttom {
		.loginType {
			display: flex;
			padding: 350rpx 150rpx 150rpx 150rpx;
			justify-content:space-between;
			
			.item {
				display: flex;
				flex-direction: column;
				align-items: center;
				color: $u-content-color;
				font-size: 28rpx;
			}
		}
		
		.hint {
			padding: 20rpx 40rpx;
			font-size: 20rpx;
			color: $u-tips-color;
			
			.link {
				color: $u-type-warning;
			}
		}
	}
}
</style>

